.swipeButtons {
    position: fixed;
    bottom: 5vh;
    display: flex;
    width: 100%;
    justify-content: space-evenly;
}

.swipeButtons .MuiIconButton-root {
    background-color: white;
    box-shadow: 0px 10px 53px 0px rgba(0, 0, 0, 0.3) !important;
}

.swipeButtons__repeat {
    padding: 2vw !important;
    color: #f5b748;
}

.swipeButtons__left {
    padding: 2vw !important;
    color: #ec5e6f;
}

.swipeButtons__star {
    padding: 2vw !important;
    color: #62b4f9;
}

.swipeButtons__right {
    padding: 2vw !important;
    color: #76e2b3;
}

.swipeButtons__lightening {
    padding: 2vw !important;
    color: #915dd1;
}